@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap);
html{
    scroll-behavior: smooth;
}

.navbar {
    width: 100%;
    height: 100px;

    display: flex;
    align-items: center;
    justify-content: space-between;
    position: fixed;
    z-index: 5000;

    padding-left: 125px;
    padding-right: 80px;

    font-family: 'Roboto';
    background: rgba(255, 255, 255, 0.8);
}

.menuNavbar ul {
    display: flex;
    list-style: none;
}

.menuNavbar ul li {
    margin-left: 32px;

    font-size: 16px;
    line-height: 19px;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);

    cursor: pointer;
    transition: 0.4s;
}
.menuNavbar ul li a {
    text-decoration: none;
    color: #010101;
}

.borderBottom {
    border-bottom: 1px solid #010101;
}
.botaoHamburguer {
    display: none;
}

@media (max-width: 767px) {
    .navbar {
        padding: 0px 35px;
    }
    .logoNavBar {
        width: 75px;
    }

    .menuList {
        width: 100vw;
        height: auto;
        padding: 0px 30px;
        margin-top: 100px;

        flex-direction: column;
        text-align: center;

        background: #F1F1F1;

        position: fixed;
        top: 0px;
        left: 0px;
        z-index: 2;

        transform: translateY(-145%);
        transition: .4s;
    }
    .menuNavbar ul li {
        margin: 0px;
        padding: 20px 0px;

        font-size: 7vw;
        text-shadow: none;
    }
    .menuNavbar ul li ~ li {
        border-top: 1px solid #C4C4C4;
    }

    .botaoHamburguer {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        width: 50px;
        height: 35px;
        cursor: pointer;
    }

    .activeMenu {
        transform: translateY(0%);
    }

    .activeMenuNavbar {
        background: rgba(255, 255, 255, 1);
    }

    .activeMarcadores {
        display: none;
    }

    .hamburguer {
        display: block;
        background-color: black;
        width: 50px;
        height: 4px;
        position: relative;
        border-radius: 10px;
    
        transition: 0.5s ease-in-out;
    
        z-index: 9999;
    }
}
#imagemBanner {
    width: 100%;
    min-height: 100vh;
    background: no-repeat;
    background-size: cover;
    background-position: center;
}
body, html {
  margin: 0;
}

.post-list {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 100vh;
  width: 100%;
  background-color: rgb(213, 211, 221);
}

.post-card {
  width: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  transition: box-shadow .3s ease-in-out;
  margin: 15px 0;
  cursor: pointer;
}

.post-card:hover {
  box-shadow: 0px 10px 20px #838383;
}

.post-card img {
  width: 100%;
}

.post-card p {
  margin: 0;
  padding: 10px 0;
  width: 100%;
  text-align: center;
  background-color: white;
  font-family: Nunito, sans-serif;
  letter-spacing: 5px;
  text-transform: uppercase;
}
.purpose-box {
    display: flex;
}
.purpose-box h2 {
    font-weight: 400;
}
.purpose-box img {
    width: 58px;
    margin-bottom: 18px;
}

.left-part {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    padding-right: 25px;
}

.vertical-divisor {
    width: 1px;

    background-color: #ffffff;
}

.right-part {
    display: flex;
    align-items: center;

    margin-left: 12px;
}
.quemSomos {
    background-image: url(/static/media/backgroundSection.8865cd6c.svg);
    background-repeat: no-repeat;
    background-size: cover;

    display: flex;

    color: #FFFFFF;
    font-family: 'Roboto';
}
.quemSomos p {
    font-size: 1.2vw;
}

.yellow-line {
    width: 10px;

    margin-left: 18px;

    background-color: #FFED00;
}

.quemSomos-container {
    width: 100%;
    height: 100%;

    display: flex;
    align-items: center;
    justify-content: center;
}
.quemSomos-content {
    width: 87vw;

    display: flex;
    flex-direction: column;
    justify-content: space-between;

    padding: 60px 0px;
}

.line-one {
    display: flex;
    justify-content: space-between;

    margin-bottom: 46px;
}
.title {
    width: 50%;
}
.title h1 {
    margin-bottom: 20px;

    font-size: 3vw;
}
.pic {
    width: 30vw;
    height: 20vw;

    
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;

    border: 5px solid #FFED00;
    box-sizing: border-box;
    border-radius: 250px 250px 100px 100px;
}

.line-two {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    grid-row-gap: 40px;
    grid-column-gap: 120px;
}

.right-part {
    margin-left: 25px;
}

.logoMaraca {
    display: none;
}
@media (max-width: 1024px) {
    .quemSomos-content {
        height: auto;
    }
    .quemSomos p {
        font-size: 2.2vw;
    }
    .pic {
        width: 42vw;
        height: 30vw;

        margin-left: 58px;
    }

    .line-one h1 {
        font-size: 2.5rem;

        margin-bottom: 24px;
    }

    .line-two {
        grid-template-columns: 1fr 1fr;
        grid-template-rows: 1fr 1fr ;
        grid-row-gap: 35px;
    }
    .line-two img {
        width: 72px;
    }


}

@media (max-width: 767px) {

    .yellow-line, .pic, .vertical-divisor {
        display: none
    }

    .quemSomos-content {
        width: 95%;

        justify-content: center;
        align-items: center;

        border: 3px solid #FFED00;
        padding: 4vw;
        box-sizing: border-box;

        margin: 12px 0px;
    }
    .quemSomos p {
        font-size: 14px;
    }

    .line-one {
        justify-content: center;

        margin-bottom: 0px;
    }
    .title {
        width: 100%;

        text-align: center;
    }
    .title h1 {
        font-size: 8vw;
        margin-bottom: 8px;
    }

    .line-two {
        grid-template-columns: 1fr;
        grid-template-rows: auto auto auto;
        grid-row-gap: 0px;
    }
    .purpose-box {
        flex-direction: column;
        align-items: center;
        text-align: center;

        margin-top: 52px;
    }
    .purpose-box img {
        width: 54px;
        margin-bottom: 4px;
    }

    .left-part {
        padding: 0px;
        margin-bottom: 8px; 
    }
    .left-part img {
        margin-bottom: 4px;
    }
    .right-part {
        margin: 0px;
    }

    .logoMaraca {
        width: 55px;
        display: block;

        margin-top: 16px
    }
}


.robosSection {
    background-color: #ECECEC;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.content {
    width: 87%;
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-top: 66px;
    margin-bottom: 66px;
}

.content h1 {
    align-self: flex-start;
    font-family: Roboto;
    font-style: normal;
    font-weight: bold;
    font-size: 40px;
    line-height: 47px;
}

.buttonsSection {

    display: flex;
    justify-content: space-between;
    margin-top: 47px;
    width: 84%;
}

.buttonsSection button {
    background: #FFFFFF;
    width: 192px;
    height: 56px;
    border: 0px solid #010101;
    box-sizing: border-box;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 8px;
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 19px;
    cursor: pointer;
    outline: none;
}


.carousel {
    width: 84%;
    margin-top: 30px;
    height: 69%;
}

.card {
    margin-top: 30px;
    height: 300px;
    display: flex !important;
    flex-direction: row !important;
}

.cardText {

    width: 50%;
    display: flex;
    flex-direction: column;
}

.card h2 {
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 28px;
    line-height: 33px;
}

.card p {
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 19px;
    margin-top: 40px;
}

.cardImage {
    width: 50%;
    background: no-repeat;
    background-size: contain;
    background-position: center;
}

.slick-dots li.slick-active button:before {
    margin-top: 14px;
    opacity: 1;
    color: yellow;
}

.slick-dots li button:before {
    font-size: 16px;
    margin-top: 14px;
}

.slick-dots li button:before {
    font-family: 'slick';
    font-size: 17px;
    line-height: 20px;
    position: absolute;
    top: 0;
    left: 0;
    width: 18px;
    height: 18px;
    content: '•';
    text-align: center;
    opacity: 1;
    color: white;
    border: 1px solid #010101;
    border-radius: 100px;
    box-sizing: border-box;
    -webkit-font-smoothing: antialiased;
}

.prev {
    position: absolute;
    background: url(/static/media/chevronLeft.355acdad.svg);
    margin-top: 310px;
    width: 14px;
    height: 26px;
    border: none;
    align-self: flex-start;
    cursor: pointer;
    outline: none;
}

.next {
    position: absolute;
    background: url(/static/media/chevronRight.48bdc653.svg);
    margin-top: 310px;
    width: 14px;
    height: 26px;
    border: none;
    align-self: flex-end;
    cursor: pointer;
    outline: none;
}

.slick-prev:before {
    display: none;
}

.slick-next::before {
    display: none;
}

@media(max-width: 1200px) {
    .carousel {
        margin-top: 15px;
    }

    .card {
        height: 250px;
    }

}


@media(max-width: 820px) {

    .buttonsSection {
        margin-top: 30px;
        display: grid;
        grid-template-rows: 1fr 1fr;
        grid-template-columns: 1fr 1fr;
        grid-row-gap: 27px;
        grid-template-areas: 
        "botaoAI botaoAC"
        "botaoCombate botaoCombate";
    }

    .buttonsSection button {
        margin: auto;
        width: 200px;
        height: 40px;
    }

    #botaoCombate {
        grid-area: botaoCombate
    }

    #botaoAutonomosIndividuais {
        grid-area: botaoAI
    }

    #botaoAutonomosColetivos {
        grid-area: botaoAC
    }

    .carousel {
        margin-top: 15px;
    }

    .prev {
        margin-top: 330px;
    }

    .next {
        margin-top: 330px;
    }

    .card p {
        font-size: 14px;
        margin-top: 10px;
    }

}

@media(max-width: 555px) {
    .content {
        justify-content: center;
        align-items: none;
    }

    .content h1 {
        align-self: center;
        font-weight: normal;
        font-size: 36px;
        line-height: 42px;
    }

    .buttonsSection {
        width: 100%;
        margin-top: 20px;
        display: grid;
        grid-template-rows: 1fr 1fr;
        grid-template-columns: 1fr 1fr;
        grid-row-gap: 27px;
        grid-template-areas:
            "botaoAI botaoAC"
            "botaoCombate botaoCombate";
    }

    .buttonsSection button {
        font-size: 14px;
        width: 200px;
        height: 40px;
    }

    #botaoCombate {
        grid-area: botaoCombate
    }

    #botaoAutonomosIndividuais {
        margin-left: 0px;
        grid-area: botaoAI
    }

    #botaoAutonomosColetivos {
        margin-right: 0px;
        grid-area: botaoAC
    }

    .card {
        flex-direction: column-reverse !important;
        height: 330px;
        margin-top: 20px;
    }

    .cardText {
        width: 100%;
        height: 50%;
        align-items: center;
    }

    .cardImage {
        width: 100%;
        height: 50%;
        background: no-repeat;
        background-size: contain;
        background-position: center;
    }

    .card h2 {
        margin-top: 23px;
    }

    .card p {
        font-size: 12px;
        line-height: 14px;
        margin-top: 10px;
        text-align: center;
    }

    .prev {
        margin-top: 30px;
    }

    .next {
        margin-top: 30px;
    }
    
}

@media(max-width: 470px) {
    .buttonsSection button {
        width: 150px;
        height: 40px;
    }
}

@media(max-width: 360px) {
    .buttonsSection button {
        font-size: 12px;
        width: 130px;
        height: 40px;
    }

    .buttonsSection {
        grid-row-gap: 15px;
    }

    .card {
        margin-top: 15px;
        height: 345px;
    }

    .buttonsSection {
        margin-top: 15px;
    }

    .card h2 {
        font-size: 24px;
        margin-top: 15px;
    }
    
}

@media(max-width: 327px) {
    .buttonsSection button {
        font-size: 10px;
        width: 110px;
        height: 40px;
    }

    .card p {
        font-size: 10px;
    }
}

@media(max-width: 260px) {
    .buttonsSection button {
        font-size: auto;
        width: auto;
        height: 40px;
    }

    .content h1 {
        font-size: 30px;
    }
}
.contato {
    background-image: url(/static/media/backgroundSection.8865cd6c.svg);
    background-repeat: no-repeat;
    background-size: cover;

    display: flex;

    font-family: 'Roboto';
    color: white;
}
.yellow-line-contato {
    width: 10px;

    margin-left: 18px;

    background-color: #FFED00;
}
.contato h1 {
    font-size: 3vw;

    margin-bottom: 32px;
}
.contato-container {
    width: 100%;

    display: flex;
    align-items: center;
    justify-content: flex-end;
}
.contato-content {
    padding: 60px 0px 50px 5vw;

    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr auto;
    grid-template-areas:
    "form collaborators"
    "form social-medias";
    grid-column-gap: 80px;
}

.form-contato {
    display: flex;
    flex-direction: column;
    grid-area: form;
}
.form-contato form {
    display: flex;
    flex-direction: column;
}
.form-contato label {
    margin-bottom: 8px;
}
.form-contato input {
    height: 36px;
}
.form-contato textarea {
    height: 160px;

    resize: none;

    padding-top: 8px;
}
.button-submit {
    width: 50%;
    margin-bottom: 0px !important;

    background: #FFED00;
    border: 1px solid #010101;
    border-radius: 8px;

    font-family: 'Roboto';
    font-size: 16px;
    font-weight: 400;

    cursor: pointer;
}
.form-contato form input,textarea {
    margin-bottom: 16px;
    padding-left: 12px;

    outline: none;
    border: 1px solid #010101;
    border-radius: 8px;
    font-family: 'Roboto';

    box-sizing: border-box;
}

.collaborators {
    width: 50vw;

    overflow: hidden;
}

.social-media {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;

    grid-row-gap: 30px;
}
@media (max-width: 767px) {

    .contato {
        flex-direction: column;
    }
    .contato-container {
        justify-content: center;
    }
    .contato-content {
        width: 100%;
        grid-template-columns: 100%;
        grid-template-rows: auto auto auto;
        grid-template-areas:
        "collaborators"
        "form"
        "social-medias";
        grid-column-gap: 0px;

        padding: 0px;
    }

    .yellow-line-contato {
        width: 100%;
        height: 6px;

        margin: 10px 0px 20px 0px;
    }

    .collaborators {
        width: 100%;
        margin-bottom: 50px;
    }
    .collaborators h1 {
        margin-bottom: 20px;
        font-size: 8vw;
        text-align: center;
    }
    

    .form-contato {
        margin-bottom: 24px;
    }
    .form-contato h1 {
        text-align: center;
        font-size: 8vw;

        margin-bottom: 16px;
    }
    .form-contato form {
        padding: 0px 16px;
    }
    .form-contato input {
        height: 24px;
    }
    .form-contato .button-submit {
        height: 30px;

        margin: auto;
        padding: 0px;
    }

    .social-media {
        background: #010101;
        padding: 27px 18px 12px 18px;
        
        grid-row-gap: 16px;
    }
    .social-media .info-box img {
        width: 24px;
        height: 24px;
    }
    .social-media .info-box span {
        font-size: 10px;
    }

}
.info-box {
    display: flex;
    align-items: center;

    text-decoration: none;
    color: white;
}
.info-box div {
    display: flex;
    flex-direction: column;
}
.info-box img {
    width: 28px;
    height: 28px;

    margin-right: 8px;
}
.info-box span {
    font-size: 12px;

    font-family: 'Roboto';
}
* {
    outline: none
}

.collaborators .slide {
    width: 51.5vw;
}
.each-collaborators {
    width: calc(25.75vw - 18px);
    height: 240px;

    background: white;
    border-radius: 8px;

    display: flex !important;
    align-items: center;
    justify-content: center;
}
.each-collaborators img {
    width: 200px;
}

.information {
    background: url(/static/media/backgroundHover.f46b45d4.svg);
    background-color: rgba(0, 0, 0, 0.2);
    background-repeat: no-repeat;
    background-position: bottom;
    background-size: cover;
    border-radius: 8px;

    color: #000000;

    position: absolute;
    z-index: 9999;

    width: calc(25.75vw - 16px);
    height: 240px;
    padding: 0px 0px 18px 12px;

    opacity: 0;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;

    transition: 0.4s;
}
.information:hover  {
    opacity: 1;
}
.information h3 {
    font-size: 14px;
}
.information p {
    font-size: 10px;
    width: 60%;
}

.collaborators .slick-slide {
    margin-right: 32px;

    width: calc(25.75vw - 16px) !important;
    height: 240px !important;
    border-radius: 8px;
}
.collaborators .slick-list {
    border-radius: 8px 0px 0px 8px;
}

@media (max-width: 767px) {
    .collaborators .slide {
        width: 100vw;
    }
    .collaborators .slide p {
        font-size: 10px;
    }
    .collaborators .slide img {
        width: 80px;
    }
    .collaborators .slick-list {
        border-radius: 0px;
    }
    .collaborators .slick-slide {
        width: 100vw !important;
        height: 186px !important;

        padding: 0px calc(30vw/2);
    }

    .information {
        width: auto;
        width: initial;
        height: auto;
        height: initial;

        opacity: 1;
        background: none;
        position: static;
        position: initial;
        padding: 0px 8px;
    }

    .each-collaborators {
        flex-direction: column;
        align-items: center;
        justify-content: center;

        height: 176px;
        
        text-decoration: none;
    }
    .each-collaborators img {
        margin-bottom: 8px
    }
    .each-collaborators h3 {
        display: none;
    }
    .each-collaborators p {
        font-size: 10px;
        width: 100%;
        text-align: center;
    }
}
